import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Navbar from './Navbar';

const LoginPage = () => {
  const navigate = useNavigate();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();

    if (email === 'admin@example.com' && password === 'password123') {
      navigate('/dashboard');
    } else {
      setError('Credenciales invlidas, por favor intente de nuevo.');
    }
  };

  return (
    <div className="min-h-screen bg-black">
      {/* Navbar fijo en la parte superior */}
      <Navbar className="fixed top-0 left-0 w-full z-50 bg-gray-800 shadow-lg" />

      {/* Espaciado para evitar que el contenido quede detrs del Navbar */}
      <div className="pt-20 flex items-center justify-center">
        <div className="w-full max-w-sm p-6 bg-gray-700 rounded-lg shadow-lg">
          <h2 className="text-3xl font-semibold text-center text-white mb-6">Hola!</h2>

          {error && <p className="text-red-500 text-sm text-center mb-4">{error}</p>}

          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-300">Correo Electrnico</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full mt-1 p-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="ejemplo@correo.com"
                required
              />
            </div>

            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-300">Contrasea</label>
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full mt-1 p-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="********"
                required
              />
            </div>

            <div className="flex justify-between items-center">
              <div>
                <input type="checkbox" id="remember" className="mr-2" />
                <label htmlFor="remember" className="text-sm text-gray-300">Recordarme</label>
              </div>
              <a href="#" className="text-sm text-white hover:underline">Olvidaste tu contrasea?</a>
            </div>

            <button
              type="submit"
              className="w-full py-2 mt-4 bg-gray-700 text-white font-semibold rounded-full hover:bg-white hover:text-black focus:outline-none border border-white"
            >
              Iniciar Sesin
            </button>
          </form>

          <div className="text-center mt-6">
            <p className="text-sm text-gray-600">
              No tienes cuenta?{' '}
              <a href="/register" className="text-white hover:underline">Regstrate aqu</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
